.filedTabWrap {
  margin-bottom: 32px;
}

.filedTab {
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
  border-bottom: dotted 1px #999;
  margin-bottom: 16px;
  z-index: 0;
  p {
    display: inline-block;
    margin: 0;
    font-size: 14px;
    background-color: #fff;
    z-index: 1;
    margin-bottom: -4px;
  }
}

.tabName {
  padding-right: 10px;
  font-weight: 300;
  color: #121212;
}

.tabValue {
  text-align: right;
  padding-left: 10px;
  color: #999;
  font-weight: 300;
  max-width: 141px;
}
