.main {
  display: flex;
  height: 100%;
}
.bookNow {
  padding: 0;
  width: 100%;
  display: flex;
  flex-direction: column;
  height: 100%;
  box-sizing: border-box;
}
.slider {
  display: none;
}
.header {
  padding-right: 16px;
  margin-top: 24px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: flex-end;
}

.title {
  color: #0ec261;
  font-size: 30px;
  font-weight: 700;
  margin: 0;
  margin-bottom: 8px;
}

.location {
  color: #999999;
  font-size: 14px;
  margin: 0;
  height: 18.33px;
  display: flex;
  align-items: center;
  justify-content: center;
  img {
    width: 17px;
    height: 20px;
    margin-right: 8px;
  }
}

.body {
  align-items: flex-start;
  height: 100%;
  display: flex;
  padding-top: 33px;
  div {
    width: 100%;
    h1 {
      font-size: 70px;
      color: #121212;
      font-size: 32px;
      line-height: 32px;
      font-weight: 700;
      margin: 0 0 16px 16px;
      span {
        color: #0ec261;
        display: block;
      }
    }
    p {
      font-size: 18px;
      color: #999;
      margin: 0 0 32px 16px;
    }
  }
  button {
    margin-left: 0px;
    border-radius: 0;
    width: 100%;
  }
}

.footer {
  display: flex;
  flex-direction: column-reverse;
  justify-content: space-between;
  align-items: flex-end;
  background: #151b1f;
  padding: 16px;
  p {
    font-size: 13px;
    color: #fff;
    margin: 0 0 8px;
  }
  a {
    font-size: 13px;
    display: inline-block;
    &.phone {
      color: #0ec261;
      text-decoration: none;
      transition: all 0.3s ease;
      &:hover {
        transition: all 0.3s ease;
      }
      &:active {
        color: #0ec261;
      }
    }
  }
}

@media screen and (min-width: 425px) {
  .body button {
    margin-left: 16px;
    border-radius: 8px;
    width: 250px;
  }
}

@media screen and (min-width: 768px) {
  .title {
    margin-bottom: 0;
  }
  .body {
    align-items: center;
    padding-top: 0px;
    button {
      margin-left: 0px;
    }
    div {
      h1 {
        margin-left: 0;
        margin: 0 0 34px;
        font-size: 70px;
        line-height: 65px;
        span {
          margin-left: 0;
        }
      }
      p {
        font-size: 26px;
        margin-left: 0px;
        margin: 0 0 60px;
      }
    }
  }

  .header {
    padding-left: 0px;
    margin-top: 0;
    flex-direction: row;
    align-items: baseline;
  }

  .bookNow {
    padding: 32px 32px 36px 32px;
  }

  .footer {
    flex-direction: row;
    justify-content: space-between;
    align-items: baseline;
    background: #fff;
    padding: 0;
    p {
      color: #999;
      margin-bottom: 0;
    }
    a {
      &.phone {
        color: #121212;
        transition: all 0.3s ease;
        &:hover {
          color: #0ec261;
          transition: all 0.3s ease;
        }
      }
    }
  }
}

@media screen and (min-width: 1024px) {
  .bookNow {
    width: 100%;
  }
  .slider {
    display: block;
    width: 410px;
    height: 100%;
    background: #ccc;
  }
}

@media screen and (min-width: 1439px) {
  .slider {
    display: block;
    width: 50%;
    height: 100%;
    background: #ccc;
  }
  .bookNow {
    padding: 32px 64px;
    width: 50%;
  }
}
